 .herotext {
     font-family: 'IBM Plex Sans', sans-serif;
     font-weight: 300;
     position: absolute;
     color: white;
     font-weight: 800;
     top: 45%;
     left: 14%;
     z-index: 100;
 }

 .herotext h1 {
     font-weight: 600;
     font-size: 3rem;
 }
@media (max-width:995px) {
    .herotext{
        font-weight: 200;
        top: 40%;
        left:3%;
    }
    .herotext h1 {
        font-weight: 400;
    }
    }

@media (max-width:645px) {
    .herotext {
        font-weight: 200;
        top: 40%;
        left: 1%;
    }

    .herotext h1 {
        font-weight: 400;
    }
    .button-right-mob {
         top: 6%;
         left: 75%;
         right:20%;
    }
}