.search{
 background: #F2F2F2;
 border-radius: 25px;
 padding: 8px 40px 8px 7px;
 color: #5C5C5C;
}
input{
border: none;
background: none;
width: 275px;
outline: none;
}
.createaccount {
    position: relative;
    left: 12px;
    font-weight: 600;
}

.nav_mob{
    display: none;
}

    @media (max-width:995px) {
        .hide-mobile {
            display: none;
        }
        .content-nav{
            display: none;
        }
        .content_main{
            display: none;
        }
        .nav_mob{
            display: block;
        }
 .nav-left-mob {
     position: absolute;
     z-index: 100;
     color: white;
     font-size: 3rem;
     /* size: 2em; */
     top: 3.5%;
     left: 3%
 }

 .button-right-mob {
     position: absolute;
     z-index: 100;
     color: white;
     top: 6%;
     left: 80%;
     padding: 1% 3% 1%;
     background-color: transparent;
     color: white;
     border: solid thin white;
 }
 .button-right-mob:hover {
     position: absolute;
     z-index: 100;
     color: white;
     top: 6%;
     left: 80%;
     right:20%;
     padding: 1% 3% 1%;
     background-color: grey;
     color: white;
     border: solid thin white;
 }
    }
    @media not all and (max-width: 995px) {
        .hide-desktop {
            display: none;
        }
        .nav_mob_head{
            position: relative;
            margin-top: 100px;
        }
    }
